import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, Image, Alert, ScrollView, Platform, TouchableHighlightComponent } from 'react-native';
import theme from '../assets/theme';
import logo from '../assets/logo-culina.png';
import wall from '../assets/wall-culina.jpg';
import AsyncStorage from '@react-native-async-storage/async-storage';
import moment from 'moment';

export default class Order extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedOrder: null,
      vip: null,
    };
  }

  async componentDidMount() {
    const { navigation, route } = this.props;

    let carts = await AsyncStorage.getItem('cart');
    let masters = await AsyncStorage.getItem('masters');
    let vip = await AsyncStorage.getItem('vip');
    let orders = await AsyncStorage.getItem('orders');
    if (carts) {
      carts = JSON.parse(carts)
    } else {
      carts = [];
    }
    if (vip) {
      vip = JSON.parse(vip)
    } else {
      vip = null;
    }
    if (orders) {
      orders = JSON.parse(orders)
    } else {
      orders = [];
    }
    this.setState({
      carts,
      vip,
      masters: JSON.parse(masters),
      orders
    }, () => {
      if (route.params.r || !orders?.[0]) {
        this.doGet();
      } else {
        this.doGet(orders?.[0])
      }
    })

    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
  }

  async didFocus() {
    const { navigation, route } = this.props;
    let carts = await AsyncStorage.getItem('cart');
    let masters = await AsyncStorage.getItem('masters');
    let vip = await AsyncStorage.getItem('vip');
    let orders = await AsyncStorage.getItem('orders');
    if (carts) {
      carts = JSON.parse(carts)
    } else {
      carts = [];
    }
    if (vip) {
      vip = JSON.parse(vip)
    } else {
      vip = null;
    }
    if (orders) {
      orders = JSON.parse(orders)
    } else {
      orders = [];
    }
    this.setState({
      carts,
      vip,
      masters: JSON.parse(masters),
      orders
    }, () => {
      if (route.params.r || !orders?.[0]) {
        this.doGet();
      } else {
        this.doGet(orders?.[0])
      }
    })
  }

  componentWillUnmount() {
    this.sub();
  }

  doGet(item) {
    const { navigation, route } = this.props;

    let serviceEntry;
    if (route.params.d === 'culina') {
      serviceEntry = 'https://www.epbrowser.com/fnb-ws/';
    }

    fetch(serviceEntry + 'api/openorders/' + (item?.recKey || route.params.r))
      .then(response => response.json())
      .then(response => {
        console.log('order', response)
        this.setState({
          selectedOrder: response
        })
      })
  }

  doPressOrder(item) {
    this.doGet(item);
  }

  doRedeem() {
    const { route, navigation } = this.props;
    const { selectedOrder, masters } = this.state;

    let serviceEntry;
    if (route.params.d === 'culina') {
      serviceEntry = 'https://www.epbrowser.com/fnb-ws/';
    }
    console.log(selectedOrder?.openorder.recKey, masters.paymentMethods.find(el => el.refType === 'P')?.pmId, Math.floor(Math.min(selectedOrder?.openorder?.balance, selectedOrder?.openorder?.vipPtsMoney)))
    fetch(serviceEntry + 'api/create-payment', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        openorderRecKey: selectedOrder?.openorder.recKey,
        pmId: masters.paymentMethods.find(el => el.refType === 'P')?.pmId,
        amt: Math.floor(Math.min(selectedOrder?.openorder?.balance, selectedOrder?.openorder?.vipPtsMoney)),
        payRef: '',
      }),
    }).then(response => response.json())
      .then(response => {
        this.doGet(selectedOrder.openorder)
      })
  }

  doPay() {
    const { route } = this.props;
    const { selectedOrder } = this.state;
    window.location.href = 'https://www.epbrowser.com/fnb-ws/api/order/' + selectedOrder?.openorder.recKey + '/redirect-to-payment-url'
      + '?backUrl=' + encodeURIComponent('https://www.epbrowser.com/bistro-ext/start?d=' + route.params.d + '&o=' + route.params.o + '&l=' + route.params.l + '&s=' + route.params.s + '&t=' + route.params.t)
      + '&redirectUrl=' + encodeURIComponent('https://www.epbrowser.com/bistro-ext/start?d=' + route.params.d + '&o=' + route.params.o + '&l=' + route.params.l + '&s=' + route.params.s + '&t=' + route.params.t)
      + '&notifyUrl=' + encodeURIComponent('https://www.epbrowser.com/fnb-ws/api/order/on-notify')
  }


  render() {
    const { navigation, route } = this.props;
    const { selectedOrder, vip, orders, masters } = this.state;
    console.log('render', selectedOrder, orders)
    return (
      <View style={{ backgroundColor: 'white', minHeight: window.innerHeight }}>
        <View style={{ height: 40, margin: 20, marginBottom: 0, flexDirection: 'row', justifyContent: 'space-between' }}>
          <TouchableOpacity onPress={() => navigation.navigate('Menu', {
            d: route.params.d,
            l: route.params.l,
            o: route.params.o,
            s: route.params.s,
            t: route.params.t,
          })}>
            <Image
              style={{ height: 40, width: 80 }}
              source={{
                uri: logo
              }}
              resizeMode='contain'
            />
          </TouchableOpacity>
          <View style={{ flex: 1, flexDirection: 'row-reverse', }}>
            <TouchableOpacity style={{ backgroundColor: theme.paid, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => navigation.navigate('Order', {
                d: route.params.d,
                l: route.params.l,
                o: route.params.o,
                s: route.params.s,
                t: route.params.t,
                r: selectedOrder?.openorder?.recKey,
              })}>
              <MaterialCommunityIcons name="clipboard-text" size={22} color={theme.grey9} />
            </TouchableOpacity>
            <View style={{ backgroundColor: theme.grey9, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}>
            </View>
            <View style={{ backgroundColor: theme.grey9, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}>
            </View>
          </View>
        </View>
        <View style={{ height: 24, flexDirection: 'row-reverse', marginHorizontal: 20, alignItems: 'center' }}>
          <Text style={{ width: 50, fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey1 }}>ORDER</Text>
          <Text style={{ width: 50, fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey1 }}></Text>
          <Text style={{ width: 50, fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey1 }}></Text>
          <Text style={{ flex: 1, fontFamily: 'regular', fontSize: 10, textAlign: 'left', color: theme.grey1 }}>{route.params.t ? `TABLE ${route.params.t}` : 'TAKE AWAY'}    {vip?.firstName?.toUpperCase() || 'GUEST'}    {moment().format('LT')}</Text>
        </View>

        {
          orders?.map(el =>
            <View key={el.recKey.toString()} style={{ opacity: el.recKey === selectedOrder?.openorder?.recKey ? 1 : 0.2 }}>
              <TouchableOpacity style={{ padding: 10, paddingTop: 20, flexDirection: 'row', borderBottomWidth: 1, borderBottomColor: theme.grey8, backgroundColor: 'white', alignItems: 'baseline', justifyContent: 'space-between' }}
                onPress={() => this.doPressOrder(el)}>
                <Text style={{ fontFamily: 'demi', fontSize: 20, color: theme.grey1 }}>Order #{el.recKey}</Text>
                <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>{moment(el.createDate).format('YYYY-MM-DD    HH:mm')}</Text>
              </TouchableOpacity>
              <View style={{ marginTop: 10 }}></View>
              {
                selectedOrder?.openorder?.recKey === el.recKey &&
                <View>
                  {
                    selectedOrder.openorderItems?.map(item =>
                      <View key={item.recKey.toString()} style={{ flexDirection: 'row', padding: 10 }}>
                        <Text style={{ textAlign: 'center', width: 40, fontFamily: 'demi', fontSize: 18 }}>{item.orderQty}</Text>
                        <View style={{ flex: 1 }}>
                          <Text style={{ fontFamily: 'demi', fontSize: 18 }}>{item.menuName}</Text>
                          <Text style={{ fontFamily: 'regular', fontSize: 16 }}>{item.menuMod}</Text>
                        </View>
                        <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 18 }}>{item.lineTotal.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
                      </View>
                    )
                  }
                  <View style={{ flexDirection: 'row', padding: 10, alignItems: 'baseline' }}>
                    <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 18 }}>Total</Text>
                    <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 20 }}>{selectedOrder?.openorder?.grandTotal.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
                  </View>
                  {
                    selectedOrder?.openorderPospays?.map(item =>
                      <View key={item.recKey.toString()} style={{ flexDirection: 'row', paddingHorizontal: 10, alignItems: 'baseline' }}>
                        <Text style={{ fontFamily: 'demi', flex: 1, textAlign: 'right', fontSize: 12 }}>{item.pmId}</Text>
                        <Text style={{ textAlign: 'right', width: 80, fontFamily: 'demi', fontSize: 14 }}>{item.payMoney.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
                      </View>
                    )
                  }
                  <View style={{ flexDirection: 'row', margin: 10 }}>
                    {
                      (Math.floor(Math.min(selectedOrder?.openorder?.balance, selectedOrder?.openorder?.vipPtsMoney)) > 0) &&
                      <TouchableOpacity
                        style={{ flex: 1, height: 40, margin: 10, borderRadius: 20, backgroundColor: theme.grey9, justifyContent: 'center', alignItems: 'center' }}
                        onPress={() => this.doRedeem()}>
                        <Text style={{ fontFamily: 'demi', fontSize: 18 }}>Redeem ({Math.floor(Math.min(selectedOrder?.openorder?.balance, selectedOrder?.openorder?.vipPtsMoney)).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })})</Text>
                      </TouchableOpacity>
                    }
                    {
                      (selectedOrder?.openorder?.balance > 0) &&
                      <TouchableOpacity
                        style={{ flex: 1, height: 40, margin: 10, borderRadius: 20, backgroundColor: theme.grey9, justifyContent: 'center', alignItems: 'center' }}
                        onPress={() => this.doPay()}>
                        <Text style={{ fontFamily: 'demi', fontSize: 18 }}>Pay ({selectedOrder?.openorder?.balance?.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })})</Text>
                      </TouchableOpacity>
                    }
                  </View>
                  {
                    (selectedOrder?.openorder?.balance > 0) ?
                      <Text style={{ fontFamily: 'regular', marginHorizontal: 20 }}>Your order will be sent once payment has been confirmed.</Text>
                      :
                      <Text style={{ fontFamily: 'regular', marginHorizontal: 20 }}>Payment complete. We have received your order!</Text>
                  }
                </View>
              }
            </View>
          )
        }





      </View>
    );
  }

}

const styles = StyleSheet.create({
  modifierTextInput: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    paddingHorizontal: 20,
    height: 120,
    backgroundColor: theme.grey3,
  },
  menuQtyRow: {
    height: 80,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowFirst: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
    marginRight: 10,
  },
  menuQtyRowSecond: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowThird: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
    marginLeft: 10,
  },
  qtyTextInput: {
    fontFamily: 'varela-round',
    fontSize: 32,
    width: '100%',
    color: 'white',
    textAlign: 'center',
  },
  modContainer: {
    padding: 5,
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    backgroundColor: theme.grey4,
  },
  modButton: {
    paddingHorizontal: 20,
    minHeight: 32,
    justifyContent: 'center',
    borderRadius: 16,
    backgroundColor: theme.grey2,
    margin: 5,
  },
  modText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 20,
  },
});
