import { Component } from 'react';
import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View, Platform, Image, Linking } from 'react-native';
import { NavigationContainer, } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import * as Font from 'expo-font';

import Menu from './src/Menu';
import Item from './src/Item';
import Order from './src/Order';
import Cart from './src/Cart';
import Pay from './src/Pay';
import Pay2 from './src/Pay2';
import Start from './src/Start';
import Login from './src/Login';
import Error from './src/Error';

const Stack = createNativeStackNavigator();

const linking = {
  config: {
    screens: {
      Start: {
        path: 'bistro-ext/start',
      },
      Login: {
        path: 'bistro-ext/login',
      },
      Menu: {
        path: 'bistro-ext/menu',
      },
      Item: {
        path: 'bistro-ext/item',
      },
      Order: {
        path: 'bistro-ext/order',
      },
      Cart: {
        path: 'bistro-ext/cart',
      },
      Pay: {
        path: 'bistro-ext/pay',
      },
      Error: {
        path: 'bistro-ext/error',
      },
    },
  },
};

export default class App extends Component {

  state = {
    loading: true,
  }

  async componentDidMount() {

    // ttf for native, woff for web
    await Font.loadAsync({
      'regular': require('./assets/AvenirNextLTPro-Regular.otf'),
      'demi': require('./assets/AvenirNextLTPro-Demi.otf'),
      'bold': require('./assets/AvenirNextLTPro-Bold.otf'),
      'light': require('./assets/AvenirNextLTPro-It.otf'),
    });
    this.setState({ loading: false })
  }

  render() {
    return (
      <NavigationContainer
        theme={{
          dark: false,
          colors: {
            // primary: 'rgb(255, 45, 85)',
            background: 'white',
            // card: 'rgb(255, 255, 255)',
            // text: 'rgb(28, 28, 30)',
            // border: 'rgb(199, 199, 204)',
            // notification: 'rgb(255, 69, 58)',
          },
        }}
        linking={linking}>
        <StatusBar hidden />
        <Stack.Navigator
          // initialRouteName="Start"
          screenOptions={{
            headerShown: false,
            animation: 'none',
            gestureEnabled: false,
          }}>
          <Stack.Screen name="Start" component={Start} options={Start.navigationOptions} />
          <Stack.Screen name="Login" component={Login} options={Login.navigationOptions} />
          <Stack.Screen name="Menu" component={Menu} options={Menu.navigationOptions} />
          <Stack.Screen name="Item" component={Item} options={Item.navigationOptions} />
          <Stack.Screen name="Cart" component={Cart} options={Cart.navigationOptions} />
          <Stack.Screen name="Pay" component={Pay} options={Pay.navigationOptions} />
          <Stack.Screen name="Pay2" component={Pay2} options={Pay2.navigationOptions} />
          <Stack.Screen name="Order" component={Order} options={Order.navigationOptions} />
          <Stack.Screen name="Error" component={Error} options={Error.navigationOptions} />
        </Stack.Navigator>
      </NavigationContainer>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
