import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, Image, TextInput, ScrollView, Linking } from 'react-native';
import theme from '../assets/theme';
import login from '../assets/login-culina.png';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userId: '',
      mode: 'USER', // USER, PASSWORD
      password: '',
      warning: null,
      sent: false,
    };
  }

  async componentDidMount() {
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
    console.log('start', await Linking.getInitialURL())
  }

  async didFocus() {
    // window.history.replaceState(null, '', await Linking.getInitialURL());
  }

  componentWillUnmount() {
    this.sub();
  }

  doSearchSimple() {
    const { navigation, route } = this.props;
    const { userId } = this.state;
    if (userId === '') {
      this.firstTextInput.focus()
      return;
    }

    let serviceEntry;
    if (route.params.d === 'culina') {
      serviceEntry = 'https://www.epbrowser.com/fnb-ws/';
    }

    fetch(serviceEntry + 'api/vendor/memberson/search-simple', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        searchString: userId,
      }),
    })
      .then(response => response.json())
      .then(response => {
        console.log(response);
        if (response?.length > 1) {
          this.setState({
            warning: 'Multiple records found. Please try again.'
          })
        } else if (response?.length === 0) {
          this.setState({
            warning: 'Invalid email or phone. Please try again.'
          })
        } else if (response?.length === 1) {
          this.setState({
            warning: null,
            userId: '',
            selectedVip: response[0],
            mode: 'PASSWORD',
          }, () => { this.secondTextInput && this.secondTextInput.focus() })
        }
      })
  }

  doForget() {
    const { navigation, route } = this.props;
    const { userId, password, selectedVip } = this.state;

    let serviceEntry;
    if (route.params.d === 'culina') {
      serviceEntry = 'https://www.epbrowser.com/fnb-ws/';
    }

    fetch(serviceEntry + 'api/vendor/memberson/forget', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        customerNumber: selectedVip.customerNumber,
      }),
    })
      .then(response => response.json())
      .then(response => {
        this.setState({
          sent: true
        })
        // if (response?.length > 1) {
        //   this.setState({
        //     warning: 'Multiple records found. Please try again.'
        //   })
        // } else if (response?.length === 0) {
        //   this.setState({
        //     warning: 'Invalid email or phone. Please try again.'
        //   })
        // } else if (response?.length === 1) {
        //   this.setState({
        //     warning: null,
        //     userId: '',
        //     selectedVip: response,
        //     mode: 'PASSWORD',
        //   }, () => { this.secondTextInput && this.secondTextInput.focus() })
        // }
      })
  }

  async doVerify() {
    const { navigation, route } = this.props;
    const { userId, password, selectedVip } = this.state;

    if (password === '') {
      this.secondTextInput.focus()
      return;
    }

    let serviceEntry;
    if (route.params.d === 'culina') {
      serviceEntry = 'https://www.epbrowser.com/fnb-ws/';
    }

    fetch(serviceEntry + 'api/vendor/memberson/signin', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        customerNumber: selectedVip.customerNumber,
        password: password,
      }),
    })
      .then(response => response.json())
      .then(response => {
        console.log(response);
        if (response.ErrorCode) {
          this.setState({
            warning: response.Message
          })
        } else {
          AsyncStorage.setItem('vip', JSON.stringify(selectedVip));
          navigation.replace('Menu', {
            d: route.params.d,
            l: route.params.l,
            o: route.params.o,
            s: route.params.s,
            t: route.params.t,
          })
        }
      })
  }

  doRegister() {
    Linking.openURL('https://www.comoclub.com/')
  }

  render() {
    const { navigation, route, } = this.props;
    const { userId, password, mode, warning, sent, selectedVip } = this.state;
    console.log('password is', password)

    return (
      <View style={{ flex: 1, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}>
        <Image
          style={{ width: 200, height: 200 }}
          source={{
            uri: login
          }}
          resizeMode='contain'
        />
        <View style={{ width: '90%' }}>
          <View style={{}}>
            <Text style={{ fontFamily: 'demi', color: '#003A63', paddingHorizontal: 20, paddingBottom: 5 }}>Email or Mobile No.</Text>
            <View style={styles.searchContainer}>
              <View style={styles.searchIconContainer}>
                <AntDesign name='user' size={18} color={theme.grey5} />
              </View>
              <TextInput
                style={styles.searchTextInput}
                autoCorrect={false}
                disabled={mode === 'PASSWORD'}
                placeholder={'email or mobile'}
                placeholderTextColor={theme.grey5}
                ref={(input) => { this.firstTextInput = input; }}
                onSubmitEditing={() => this.doSearchSimple()}
                defaultValue={userId}
                onChangeText={(userId) => this.setState({ userId })}
              />
            </View>
            <TouchableOpacity
              disabled={mode === 'PASSWORD'}
              onPress={() => this.doRegister()}>
              <Text style={{ fontFamily: 'demi', color: '#003A63', paddingBottom: 5, textAlign: 'right', opacity: mode === 'PASSWORD' ? 0.5 : 1 }}>Not a member? Register here.</Text>
            </TouchableOpacity>
          </View>
          {
            mode === 'PASSWORD' &&
            <View>
              <Text style={{ fontFamily: 'demi', color: '#003A63', paddingHorizontal: 20, paddingBottom: 5 }}>Password</Text>
              <View style={styles.searchContainer}>
                <View style={styles.searchIconContainer}>
                  <AntDesign name='user' size={18} color={theme.grey5} />
                </View>
                <TextInput
                  style={styles.searchTextInput}
                  autoCorrect={false}
                  secureTextEntry
                  placeholder={'password'}
                  placeholderTextColor={theme.grey5}
                  ref={(input) => { this.secondTextInput = input; }}
                  onChangeText={(password) => this.setState({ password })}
                />
              </View>
              <TouchableOpacity
                disabled={sent}
                onPress={() => this.doForget()}>
                <Text style={{ fontFamily: 'demi', color: '#003A63', paddingBottom: 5, textAlign: 'right', opacity: sent ? 0.5 : 1 }}>{sent ? `OTP sent to ${selectedVip?.email}` : 'Send One-time Password'}</Text>
              </TouchableOpacity>
            </View>
          }
          <Text style={{ fontFamily: 'demi', color: 'red', paddingHorizontal: 20, paddingBottom: 5 }}>{warning || ' '}</Text>
          <TouchableOpacity style={{ width: '100%', height: 60, marginBottom: 100, alignItems: 'center', justifyContent: 'center', backgroundColor: '#003A63' }}
            onPress={() => { mode === 'USER' ? this.doSearchSimple() : this.doVerify() }}>
            <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 2, color: 'white' }}>{mode === 'USER' ? 'NEXT' : 'VERIFY'}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }

}

const styles = StyleSheet.create({
  searchContainer: {
    height: 50,
    borderRadius: 5,
    backgroundColor: theme.grey9,
    flexDirection: 'row',
    marginBottom: 10
  },
  searchIconContainer: {
    marginVertical: 10,
    marginHorizontal: 5,
    width: 40,
    alignItems: 'center',
    justifyContent: 'center',
    borderRightWidth: 1,
    borderRightColor: theme.grey7,
  },
  searchTextInput: {
    flex: 1,
    // height: 50,
    // margin: 10,
    // padding: 10,
    paddingHorizontal: 10,
    borderRadius: 5,
    // backgroundColor: 'orange',
    fontFamily: 'regular',
    fontSize: 20,
  },
});
