import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, Image, Alert, ScrollView, Platform, Animated } from 'react-native';
import theme from '../assets/theme';
import moment from 'moment';
import wall from '../assets/wall-culina.jpg';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class Item extends Component {
  constructor(props) {
    super(props);
    this.state = {
      masters: null,
      carts: [],
      animatedValue: new Animated.Value(0),

      remark: '',
      orderQty: 1,
      modRecKeys: [],
      modInput: '',
    };
  }

  async componentDidMount() {
    const { route } = this.props;
    let masters = await AsyncStorage.getItem('masters')
    masters = JSON.parse(masters);
    let carts = await AsyncStorage.getItem('cart')
    if (carts) {
      carts = JSON.parse(carts)
      console.log('herere', carts, this.props.route.params.selectedItem, decodeURI(this.props.route.params.selectedItem))
    } else {
      carts = [];
    }
    this.setState({
      masters: masters,
      selectedMenu: masters.menus.find(el => el.menuId === route.params.m),
      mods: masters.mods.filter(el => el.menuId === route.params.m),
      remark: carts.find(el => el.cacheDate === decodeURI(this.props.route.params.selectedItem))?.remark || '',
      orderQty: carts.find(el => el.cacheDate === decodeURI(this.props.route.params.selectedItem))?.orderQty || 1,
      modRecKeys: carts.find(el => el.cacheDate === decodeURI(this.props.route.params.selectedItem))?.modRecKeys || [],
      modInput: carts.find(el => el.cacheDate === decodeURI(this.props.route.params.selectedItem))?.modInput || '',
    })
  }
  componentWillUnmount() {
    console.log('im geting rekt')
  }

  doPressAdd() {
    const { orderQty } = this.state;
    this.setState({
      orderQty: orderQty + 1
    })
  }

  doPressMinus() {
    const { orderQty } = this.state;
    if (orderQty <= 1) {
      return;
    } else {
      this.setState({
        orderQty: orderQty - 1
      })
    }
  }

  doPressMod(item) {
    const { masters, modInput, modRecKeys } = this.state;

    const selectedModBundle = masters.modBundles.find(el => el.modmas.modId === item.modId);
    let modInputUpdate = modInput;
    let modRecKeysUpdate = modRecKeys;

    if (selectedModBundle.modmas.modType === 'A') {
      // any
      if (modRecKeys.some(el => el === item.recKey.toString())) {
        modInputUpdate = modInputUpdate.replace('*' + item.name + '  ', '');
        modRecKeysUpdate = modRecKeysUpdate.filter(obj => obj !== item.recKey.toString());
      } else {
        modInputUpdate += '*' + item.name + '  ';
        modRecKeysUpdate.push(item.recKey.toString());
      }
    } else {
      // either
      selectedModBundle.moditems.forEach(el => {
        modInputUpdate = modInputUpdate.replace('*' + el.name + '  ', '');
        modRecKeysUpdate = modRecKeysUpdate.filter(obj => obj !== el.recKey.toString());
      })
      if (!modRecKeys.some(el => el === item.recKey.toString())) {
        modInputUpdate += '*' + item.name + '  ';
        modRecKeysUpdate.push(item.recKey.toString());
      }
    }
    this.setState({
      modInput: modInputUpdate,
      modRecKeys: modRecKeysUpdate,
    })
  }

  async doSubmit() {
    const { navigation, route } = this.props;
    const { carts, header, orderQty, remark, modRecKeys, modInput, selectedMenu } = this.state;
    let cartsUpdate = await AsyncStorage.getItem('cart');
    if (cartsUpdate) {
      cartsUpdate = JSON.parse(cartsUpdate);
    } else {
      cartsUpdate = []
    }
    if (route.params.selectedItem) {
      cartsUpdate = cartsUpdate.filter(el => el.cacheDate !== decodeURI(route.params.selectedItem))
    }
    cartsUpdate.push(({
      ...selectedMenu,
      cacheDate: moment(),
      orderQty: orderQty,
      remark: remark,
      modRecKeys: modRecKeys,
      modInput: modInput,
    }))
    await AsyncStorage.setItem('cart', JSON.stringify(cartsUpdate))
    if (route.params.selectedItem) {
      navigation.navigate('Cart', {
        d: route.params.d,
        l: route.params.l,
        o: route.params.o,
        s: route.params.s,
        t: route.params.t,
      });
    } else {
      navigation.navigate('Menu', {
        d: route.params.d,
        l: route.params.l,
        o: route.params.o,
        s: route.params.s,
        t: route.params.t,
      });
    }
  }

  render() {
    const { navigation, route } = this.props;
    const { title, remark, orderQty, carts, animatedValue,
      selectedMenu, mods, modRecKeys, masters, } = this.state;

    return (
      <View style={{ backgroundColor: 'white', minHeight: window.innerHeight, justifyContent: 'space-between' }}>
        <View>
          <View style={{ height: 200, }}>
            <Image
              style={{
                flex: 1,
              }}
              source={{
                uri: wall
              }}
              resizeMode='cover'
            />
          </View>
          <View style={{ borderBottomWidth: 1, borderBottomColor: theme.grey8, paddingVertical: 20, }}>
            <Text style={{ fontFamily: 'demi', paddingHorizontal: 10, fontSize: 20, color: theme.grey1 }}>{selectedMenu?.menuName}<Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>  {selectedMenu?.menuNameLang}</Text></Text>
            <Text style={{ fontFamily: 'demi', fontSize: 16, marginTop: 10, paddingHorizontal: 10, color: theme.grey1 }}>{selectedMenu?.listPrice.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
          </View>

          {
            mods?.map(el =>
              <View key={el.recKey.toString()} style={{ paddingVertical: 30, paddingHorizontal: 10 }}>
                <Text style={{ fontFamily: 'demi', fontSize: 18, marginBottom: 5 }}>{el.name}<Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>{masters.modBundles.find(obj => obj.modmas.modId === el.modId)?.modmas.mustFlg === 'Y' ? '  compulsory, ' : '  optional, '}{masters.modBundles.find(obj => obj.modmas.modId === el.modId)?.modmas.modType === 'A' ? 'select any' : 'choose one'}</Text></Text>
                {
                  masters.modBundles.find(obj => obj.modmas.modId === el.modId)?.moditems.map((obj, index) =>
                    <TouchableOpacity key={obj.recKey.toString()} style={{ flexDirection: 'row', alignItems: 'center', minHeight: 40 }}
                      onPress={() => this.doPressMod(obj)}>
                      <View style={{ height: 20, width: 20, borderRadius: 10, backgroundColor: modRecKeys.some(e => e === obj.recKey.toString()) ? theme.grey3 : theme.grey9, margin: 10, marginTop: 8 }}>

                      </View>
                      <Text style={{ fontFamily: 'demi', fontSize: 15, opacity: modRecKeys.some(e => e === obj.recKey.toString()) ? 1 : 0.5 }}>{obj.name} {obj.listPrice !== 0 && `+${obj.listPrice.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`}</Text>
                    </TouchableOpacity>
                  )
                }
              </View>
            )
          }

          <View style={{ paddingVertical: 30, paddingHorizontal: 10, }}>
            <Text style={{ fontFamily: 'demi', fontSize: 18, marginBottom: 5 }}>Remarks<Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>  additional comments</Text></Text>
            <TextInput
              style={{ marginTop: 5, borderRadius: 5, backgroundColor: theme.grey9, minHeight: 50, fontFamily: 'regular', fontSize: 16, padding: 5 }}
              multiline={true}
              onChangeText={(text) => this.setState({ remark: text })}
              value={remark}
            />
          </View>
        </View>
        <View style={{ marginTop: 140 }}>
        </View>
        <View style={{
          position: 'fixed', backgroundColor: 'white', bottom: 0, left: 0, right: 0,
          shadowColor: theme.grey1,
          shadowOffset: { width: 0, height: -2 },
          shadowOpacity: 0.2,
          shadowRadius: 20,
        }}>
          <View style={{ flexDirection: 'row', height: 60, backgroundColor: 'white', }}>
            <TouchableOpacity style={{ flex: 1, alignItems: 'flex-end', justifyContent: 'center' }}
              onPress={() => this.doPressMinus()}>
              <AntDesign name="minus" size={24} color={theme.grey1} />
            </TouchableOpacity>
            <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
              <Text style={{ fontFamily: 'demi', fontSize: 24, color: theme.grey1 }}>{orderQty}</Text>
            </View>
            <TouchableOpacity style={{ flex: 1, alignItems: 'flex-start', justifyContent: 'center' }}
              onPress={() => this.doPressAdd()}>
              <AntDesign name="plus" size={24} color={theme.grey1} />
            </TouchableOpacity>
          </View>
          <TouchableOpacity style={{ margin: 10, height: 60, borderRadius: 5, backgroundColor: theme.grey8, alignItems: 'center', justifyContent: 'center', opacity: masters?.modBundles.filter(el => el.modmas.mustFlg === 'Y' && (el.modmas.globalFlg === 'Y' || mods.some(obj => obj.menuId === route.params.m && obj.modId === el.modmas.modId))).some(el => !el.moditems.some(obj => modRecKeys.some(el => el === obj.recKey.toString())))?0.2:1 }}
            disabled={masters?.modBundles.filter(el => el.modmas.mustFlg === 'Y' && (el.modmas.globalFlg === 'Y' || mods.some(obj => obj.menuId === route.params.m && obj.modId === el.modmas.modId))).some(el => !el.moditems.some(obj => modRecKeys.some(el => el === obj.recKey.toString())))}
            onPress={() => this.doSubmit()}>
            <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 2, color: theme.grey1 }}>{route.params.selectedItem ? 'EDIT' : 'ADD'}</Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }

}

const styles = StyleSheet.create({
  modifierTextInput: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    paddingHorizontal: 20,
    height: 120,
    backgroundColor: theme.grey3,
  },
  menuQtyRow: {
    height: 80,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowFirst: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
    marginRight: 10,
  },
  menuQtyRowSecond: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowThird: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
    marginLeft: 10,
  },
  qtyTextInput: {
    fontFamily: 'varela-round',
    fontSize: 32,
    width: '100%',
    color: 'white',
    textAlign: 'center',
  },
  modContainer: {
    padding: 5,
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    backgroundColor: theme.grey4,
  },
  modButton: {
    paddingHorizontal: 20,
    minHeight: 32,
    justifyContent: 'center',
    borderRadius: 16,
    backgroundColor: theme.grey2,
    margin: 5,
  },
  modText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 20,
  },
});
