import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, TextInput, Image, Alert, ScrollView, Platform, TouchableHighlightComponent } from 'react-native';
import theme from '../assets/theme';
import logo from '../assets/logo-culina.png';
import wall from '../assets/wall-culina.jpg';
import AsyncStorage from '@react-native-async-storage/async-storage';
import moment from 'moment';

export default class Item extends Component {
  constructor(props) {
    super(props);
    this.state = {
      carts: [],
      vip: null,
    };
  }

  async componentDidMount() {
    // AsyncStorage.removeItem('cart');
    let carts = await AsyncStorage.getItem('cart');
    let masters = await AsyncStorage.getItem('masters');
    let vip = await AsyncStorage.getItem('vip');
    if (carts) {
      carts = JSON.parse(carts)
    } else {
      carts = [];
    }
    if (vip) {
      vip = JSON.parse(vip)
    } else {
      vip = null;
    }
    this.setState({
      carts,
      vip,
      masters,
    })
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
  }

  async didFocus() {
    // AsyncStorage.removeItem('cart');
    let carts = await AsyncStorage.getItem('cart');
    let masters = await AsyncStorage.getItem('masters');
    let vip = await AsyncStorage.getItem('vip');
    if (carts) {
      carts = JSON.parse(carts)
    } else {
      carts = [];
    }
    if (vip) {
      vip = JSON.parse(vip)
    } else {
      vip = null;
    }
    this.setState({
      carts,
      vip,
      masters: JSON.parse(masters),
    })
  }

  componentWillUnmount() {
    this.sub();
  }

  doRemove(item) {
    const { carts } = this.state;
    let cartsUpdate = carts;
    const found = cartsUpdate.findIndex(el => el.cacheDate === item.cacheDate)
    if (found > -1) { // only splice array when item is found
      cartsUpdate.splice(found, 1); // 2nd parameter means remove one item only
    }
    this.setState({
      carts: cartsUpdate
    })
    AsyncStorage.setItem('cart', JSON.stringify(cartsUpdate));
  }

  async doSubmit() {
    const { navigation, route } = this.props;
    const { carts, vip } = this.state;

    let ordersUpdate = await AsyncStorage.getItem('orders');
    if (ordersUpdate) {
      ordersUpdate = JSON.parse(ordersUpdate);
    } else {
      ordersUpdate = [];
    }

    let serviceEntry;
    if (route.params.d === 'culina') {
      serviceEntry = 'https://www.epbrowser.com/fnb-ws/';
    }

    fetch(serviceEntry + 'api/create-openorder', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
      },
      body: JSON.stringify({
        orgId: route.params.o,
        locId: route.params.l,
        shopId: route.params.s,
        tableId: route.params.t || '',
        vipId: vip?.customerNumber || '',
        // vipId: '00800003',
        vipName: vip?.firstName || '',
        paymentPayloads: [],
        itemPayloads: carts.map(el => ({
          orderQty: el.orderQty,
          restmenuMod: el.remark ? `"${el.remark}" ${el.modInput}` : el.modInput,
          modRecKeys: el.modRecKeys.join(','),
          restmenuRecKey: el.recKey,
        }))
      }),
    }).then(response => response.json())
      .then(response => {
        // storeCart([]);
        if (response.errMsg) {
          Alert.alert(response.errMsg)
          return;
        }
        this.setState({
          carts: [],
        })
        if (response.openorder) {
          // Pay 1 is Scan QR Code to local order
          // Pay 2 is Direct to Payment
          // navigation.navigate('Pay', { id, header: response.openorder });
          AsyncStorage.removeItem('cart');
          ordersUpdate.unshift(response.openorder)
          AsyncStorage.setItem('orders', JSON.stringify(ordersUpdate))
          navigation.navigate('Order', {
            d: route.params.d,
            l: route.params.l,
            o: route.params.o,
            s: route.params.s,
            t: route.params.t,
            r: response.openorder.recKey
          })
        }
      });


  }

  render() {
    const { navigation, route } = this.props;
    const { carts, vip, masters } = this.state;
    return (
      <View style={{ backgroundColor: 'white', minHeight: window.innerHeight, justifyContent: 'space-between' }}>
        <View>
          <View style={{ height: 40, margin: 20, marginBottom: 0, flexDirection: 'row', justifyContent: 'space-between' }}>
            <TouchableOpacity onPress={() => navigation.navigate('Menu', {
              d: route.params.d,
              l: route.params.l,
              o: route.params.o,
              s: route.params.s,
              t: route.params.t,
            })}>
              <Image
                style={{ height: 40, width: 80 }}
                source={{
                  uri: logo
                }}
                resizeMode='contain'
              />
            </TouchableOpacity>
            <View style={{ flex: 1, flexDirection: 'row-reverse', }}>
              <View style={{ backgroundColor: theme.grey9, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}>
              </View>
              <TouchableOpacity style={{ backgroundColor: theme.reserved, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
                onPress={() => navigation.navigate('Cart', {
                  d: route.params.d,
                  l: route.params.l,
                  o: route.params.o,
                  s: route.params.s,
                  t: route.params.t,
                })}>
                <MaterialCommunityIcons name="shopping" size={22} color={theme.grey9} />
              </TouchableOpacity>
              <View style={{ backgroundColor: theme.grey9, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}>
              </View>
            </View>
          </View>
          <View style={{ height: 24, flexDirection: 'row-reverse', marginHorizontal: 20, alignItems: 'center' }}>
            <Text style={{ width: 50, fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey1 }}></Text>
            <Text style={{ width: 50, fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey1 }}>CART</Text>
            <Text style={{ width: 50, fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey1 }}></Text>
            <Text style={{ flex: 1, fontFamily: 'regular', fontSize: 10, textAlign: 'left', color: theme.grey1 }}>{route.params.t ? `TABLE ${route.params.t}` : 'TAKE AWAY'}    {vip?.firstName?.toUpperCase() || 'GUEST'}    {moment().format('LT')}</Text>
          </View>

          <View style={{ borderBottomWidth: 1, borderBottomColor: theme.grey8, backgroundColor: 'white', }}>
            <Text style={{ fontFamily: 'demi', padding: 10, fontSize: 20, color: theme.grey1, paddingTop: 20 }}>My Cart  <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>({carts.length})</Text></Text>
          </View>

          {
            carts?.map((item, index) =>
              <View
                key={item.cacheDate}
                style={{ flexDirection: 'row', padding: 10, borderBottomColor: theme.grey9, borderBottomWidth: 1, backgroundColor: 'white' }}>
                {
                  <Image
                    style={{ height: 80, width: 80, borderRadius: 5, marginRight: 10, }}
                    source={{
                      uri: wall
                    }}
                    resizeMode='cover'
                  />
                }
                <View style={{ flex: 1 }}>
                  <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                    <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.grey1 }}>{item.menuName}</Text>
                    <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.grey1 }}>{item.orderQty}</Text>
                  </View>
                  <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>{item.menuNameLang}</Text>
                  <Text style={{ fontFamily: 'demi', fontSize: 16, marginVertical: 10, color: theme.grey1 }}>{item.listPrice.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</Text>
                  {
                    item?.remark.length > 0 &&
                    <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>"{item.remark}"</Text>
                  }
                  {
                    item?.modInput.length > 0 &&
                    <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>{item.modInput}</Text>
                  }
                  <View style={{ flexDirection: 'row' }}>
                    <TouchableOpacity style={{ marginTop: 10, width: 100 }} onPress={() => this.doRemove(item)}>
                      <Text style={{ fontFamily: 'demi', fontSize: 16, color: theme.paid }}>Remove</Text>
                    </TouchableOpacity>
                    <TouchableOpacity style={{ marginTop: 10, width: 100 }} onPress={() => navigation.navigate('Item', {
                      d: route.params.d,
                      l: route.params.l,
                      o: route.params.o,
                      s: route.params.s,
                      t: route.params.t,
                      m: item.menuId,
                      selectedItem: item.cacheDate,
                    })}>
                      <Text style={{ fontFamily: 'demi', fontSize: 16, color: theme.grey1 }}>Edit</Text>
                    </TouchableOpacity>
                  </View>

                </View>
              </View>
            )
          }
        </View>
        <TouchableOpacity style={{ margin: 10, height: 60, borderRadius: 5, backgroundColor: theme.grey8, alignItems: 'center', justifyContent: 'center' }}
          disabled={carts.length === 0}
          onPress={() => this.doSubmit()}>
          <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 2, color: theme.grey1 }}>CHECKOUT</Text>
        </TouchableOpacity>
      </View>
    );
  }

}

const styles = StyleSheet.create({
  modifierTextInput: {
    fontFamily: 'varela-round',
    fontSize: 20,
    color: 'white',
    paddingHorizontal: 20,
    height: 120,
    backgroundColor: theme.grey3,
  },
  menuQtyRow: {
    height: 80,
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowFirst: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
    marginRight: 10,
  },
  menuQtyRowSecond: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  menuQtyRowThird: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'flex-start',
    alignItems: 'center',
    marginLeft: 10,
  },
  qtyTextInput: {
    fontFamily: 'varela-round',
    fontSize: 32,
    width: '100%',
    color: 'white',
    textAlign: 'center',
  },
  modContainer: {
    padding: 5,
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    backgroundColor: theme.grey4,
  },
  modButton: {
    paddingHorizontal: 20,
    minHeight: 32,
    justifyContent: 'center',
    borderRadius: 16,
    backgroundColor: theme.grey2,
    margin: 5,
  },
  modText: {
    fontFamily: 'varela-round',
    color: 'white',
    fontSize: 20,
  },
});
