import { Component } from 'react';
import { AntDesign, Entypo, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, SectionList, Animated, TouchableOpacity, Text, Image, TextInput, Alert, ScrollView, Platform } from 'react-native';
import theme from '../assets/theme';
import logo from '../assets/logo-culina.png';
import wall from '../assets/wall-culina.jpg';
import moment from 'moment';
import AsyncStorage from '@react-native-async-storage/async-storage';
import MenuFolder from './MenuFolder';

export default class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      masters: null,
      maps: [],
    };

    this.handlePressFolder = this.handlePressFolder.bind(this);
    this.handleViewableChanged = this.handleViewableChanged.bind(this);
  }

  async componentDidMount() {
    const masters = await AsyncStorage.getItem('masters');
    let vip = await AsyncStorage.getItem('vip');
    this.doPrepareMasters(JSON.parse(masters))
    this.setState({
      masters: JSON.parse(masters),
      vip: vip ? JSON.parse(vip) : null
    })
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
  }

  async didFocus() {
    let vip = await AsyncStorage.getItem('vip');
    this.setState({
      vip: vip ? JSON.parse(vip) : null
    })
  }

  componentWillUnmount() {
    this.sub();
  }

  doPrepareMasters(masters) {
    console.log(masters.folders.filter(el => el.foldergrpId === 'EXT').map(el => ({ title: el.name, folderId: el.folderId })))
    console.log(masters.menus)
    let maps = masters.folders.filter(el => el.foldergrpId === 'EXT').map(el => ({ title: el.name, folderId: el.folderId }));
    maps.forEach(el => {
      el.data = masters.menus.filter(item => item.folderId === el.folderId && item.foldergrpId === 'EXT')
    });
    this.setState({
      maps
    })
  }

  handlePressFolder(index) {
    console.log('here', index)
    this.sectionListRef.scrollToLocation({
      animated: false,
      sectionIndex: index,
      itemIndex: 1,
    })
  }

  handleViewableChanged({ viewableItems, changed }) {
    console.log('parent', viewableItems, changed)
    this.menuFolderRef.handleViewableChanged({ viewableItems, changed });
  }

  render() {
    const { navigation, route } = this.props;
    const { masters, maps, vip } = this.state;

    return (
      <View style={{ flex: 1, backgroundColor: 'white', }}>
        <View style={{ height: 40, margin: 20, marginBottom: 0, flexDirection: 'row', justifyContent: 'space-between' }}>
          <TouchableOpacity onPress={() => navigation.navigate('Menu', {
            d: route.params.d,
            l: route.params.l,
            o: route.params.o,
            s: route.params.s,
            t: route.params.t,
          })}>
            <Image
              style={{ height: 40, width: 80 }}
              source={{
                uri: logo
              }}
              resizeMode='contain'
            />
          </TouchableOpacity>
          <View style={{ flex: 1, flexDirection: 'row-reverse', }}>
            <TouchableOpacity style={{ backgroundColor: theme.paid, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => navigation.navigate('Order', {
                d: route.params.d,
                l: route.params.l,
                o: route.params.o,
                s: route.params.s,
                t: route.params.t,
              })}>
              <MaterialCommunityIcons name="clipboard-text" size={22} color={theme.grey9} />
            </TouchableOpacity>
            <TouchableOpacity style={{ backgroundColor: theme.reserved, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => navigation.navigate('Cart', {
                d: route.params.d,
                l: route.params.l,
                o: route.params.o,
                s: route.params.s,
                t: route.params.t,
              })}>
              <MaterialCommunityIcons name="shopping" size={22} color={theme.grey9} />
            </TouchableOpacity>
            <TouchableOpacity style={{ backgroundColor: theme.occupied, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
              onPress={() => navigation.navigate('Login', {
                d: route.params.d,
                l: route.params.l,
                o: route.params.o,
                s: route.params.s,
                t: route.params.t,
              })}>
              <MaterialCommunityIcons name="crown" size={22} color={theme.grey9} />
            </TouchableOpacity>
          </View>
        </View>
        <View style={{ height: 24, flexDirection: 'row-reverse', marginHorizontal: 20, alignItems: 'center' }}>
          <Text style={{ width: 50, fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey1 }}>ORDER</Text>
          <Text style={{ width: 50, fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey1 }}>CART</Text>
          <Text style={{ width: 50, fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey1 }}>MEMBER</Text>
          <Text style={{ flex: 1, fontFamily: 'regular', fontSize: 10, textAlign: 'left', color: theme.grey1 }}>{route.params.t ? `TABLE ${route.params.t}` : 'TAKE AWAY'}    {vip?.firstName?.toUpperCase() || 'GUEST'}    {moment().format('LT')}</Text>
        </View>
        <MenuFolder
          ref={ref => this.menuFolderRef = ref}
          folders={this.state.masters?.folders?.filter(el => el.foldergrpId === 'EXT')}
          handlePressFolder={this.handlePressFolder}
        />
        {/* <View style={{ height: 24, justifyContent: 'center', marginRight: 10 }}>
          <Text style={{ fontFamily: 'regular', fontSize: 10, textAlign: 'right', color: theme.grey1 }}>VISIT {moment().format('YYYY-MM-DD HH:MM')} AS <Text>{vip ? `${vip.firstName} ${vip.lastName}` : 'GUEST'}</Text></Text>
        </View> */}
        <SectionList
          style={{ flex: 1 }}
          ref={ref => this.sectionListRef = ref}
          // scrollEnabled={false}
          onViewableItemsChanged={this.handleViewableChanged}
          scrollEventThrottle={10}
          showsVerticalScrollIndicator={false}
          stickySectionHeadersEnabled
          sections={maps}
          ListHeaderComponent={
            <Image
              style={{ height: 120, width: '100%', }}
              source={{
                uri: wall
              }}
              resizeMode='cover'
            />
          }
          ListFooterComponent={<View style={{ marginTop: 20, marginBottom: 80 }}><Text style={{ fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey5 }}>END OF MENU</Text></View>}
          keyExtractor={(item, index) => index}
          renderItem={({ item, index }) =>
            <View style={{ flexDirection: 'row', padding: 10, borderBottomColor: theme.grey9, borderBottomWidth: 1, backgroundColor: 'white' }}>
              {
                // translateImageUrl(item.menuId) &&
                <Image
                  style={{ height: 80, width: 80, borderRadius: 5, marginRight: 10, }}
                  source={{
                    uri: wall
                  }}
                  resizeMode='cover'
                />
              }
              <TouchableOpacity style={{ flex: 1 }}
                onPress={() =>
                  navigation.navigate('Item', {
                    d: route.params.d,
                    l: route.params.l,
                    o: route.params.o,
                    s: route.params.s,
                    t: route.params.t,
                    m: item.menuId,
                  })
                }>
                <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                  <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.grey1 }}>{item.menuName}</Text>
                </View>
                <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>{item.menuNameLang}</Text>
                <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                  <Text style={{ fontFamily: 'demi', fontSize: 16, marginTop: 10, color: theme.grey1 }}>{item.listPrice.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}   </Text>
                  {/* <Text style={{ fontFamily: 'demi', fontSize: 16, marginTop: 10, color: theme.grey1 }}>2 </Text>
                  <View style={{ marginTop: 10, height: 20, width: 20, backgroundColor: theme.reserved, borderRadius: 10 }}></View> */}

                </View>
              </TouchableOpacity>
              {/* <Image
                style={{ position: 'absolute', top: 10, left: 10, height: 80, width: 80, borderRadius: 5, marginRight: 10, }}
                source={{
                  // uri: translateImageUrl(item.menuId)
                  uri: 'https://scontent.fsin14-1.fna.fbcdn.net/v/t1.6435-9/51771413_1986274018108621_8189578584143167488_n.jpg?_nc_cat=109&ccb=1-7&_nc_sid=730e14&_nc_ohc=k8NgqK1aevAAX8ajQXp&_nc_ht=scontent.fsin14-1.fna&oh=00_AfBVtiRktTIuwt_UI4TBVkoiC1dDVSzHUjDdrrLT22eqTA&oe=64CEECE2'
                }}
                resizeMode='cover'
              /> */}
            </View>
          }
          renderSectionHeader={({ section }) => (
            <View style={{ marginTop: -1, borderBottomWidth: 1, borderBottomColor: theme.grey8, backgroundColor: 'white', }}>
              <Text style={{ fontFamily: 'demi', padding: 10, fontSize: 20, color: theme.grey1, paddingTop: 20 }}>{section.title}  <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>({section.data.length})</Text></Text>
            </View>
          )}
          renderSectionFooter={({ section }) => (
            <Image
              style={{ height: 120, width: '100%', marginTop: 50 }}
              source={{
                uri: wall
              }}
              resizeMode='cover'
            />
          )}
        />
        {/* <View style={{ position: 'fixed', backgroundColor: theme.reserved, bottom: 0, left: 0, right: 0, height:80,alignItems: 'center', justifyContent: 'center',
                  shadowColor: theme.grey1,
                  shadowOffset: { width: 0, height: -20 },
                  shadowOpacity: 0.4,
                  shadowRadius: 40, }}>
        </View> */}
        <View style={{ position: 'fixed', right: 20, bottom: 20, }}>
          <TouchableOpacity style={{ backgroundColor: theme.reserved, height: 40, width: 40, borderRadius: 20, marginHorizontal: 5, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Cart', {
              d: route.params.d,
              l: route.params.l,
              o: route.params.o,
              s: route.params.s,
              t: route.params.t,
            })}>
            <MaterialCommunityIcons name="shopping" size={22} color={theme.grey9} />
            <View style={{ position: 'absolute', right:-2, top:-2, borderRadius: 162, height: 12, width: 12,backgroundColor:theme.paid }}></View>
          </TouchableOpacity>
        </View>
      </View>
    );
  }

}

const styles = StyleSheet.create({

});
